<template>
  <div class="partner-company">
    <el-container>
      <el-header class="header">
        <el-form
          :inline="true"
          :model="searchValidateForm"
          ref="searchValidateForm"
          label-width="100px"
          class="search-validate-form"
          :rules="searchValidateFormRules"
        >
          <el-form-item label="" prop="managementId">
            <!-- 选择配件名称 start -->
            <el-select
              v-model="searchValidateForm.managementId"
              filterable
              placeholder="请选择配件名称"
              clearable
            >
              <el-option
                v-for="(item, index) in searchNameOptions"
                :key="index"
                :label="item.name"
                :value="item.id"
              >
              </el-option>
            </el-select>
            <!-- 选择配件名称 end -->
          </el-form-item>

          <el-form-item label="" prop="spotId">
            <!-- 选择仓库名称 start -->
            <el-select
              v-model="searchValidateForm.spotId"
              filterable
              placeholder="请选择仓库名称"
              clearable
            >
              <el-option
                v-for="(item, index) in searchSpotNameOptions"
                :key="index"
                :label="item.name"
                :value="item.id"
              >
              </el-option>
            </el-select>
            <!-- 选择仓库名称 end -->
          </el-form-item>

          <el-form-item label="" prop="threshold">
            <!-- 选择阈值 start -->
            <el-select
              v-model="searchValidateForm.isDanger"
              filterable
              placeholder="请选择阈值"
              clearable
            >
              <el-option
                v-for="(item, index) in searchThresholdOptions"
                :key="index"
                :label="item.label"
                :value="item.value"
              >
              </el-option>
            </el-select>
            <!-- 选择阈值 end -->
          </el-form-item>
        </el-form>

        <!-- 搜索 start -->
        <el-button
          type="primary"
          icon="el-icon-search"
          @click="onSearch('searchValidateForm')"
          >搜索</el-button
        >
        <!-- 搜索 end -->

        <!-- 新增 start -->
        <el-button
          type="primary"
          icon="el-icon-circle-plus-outline"
          @click="onAdd"
          >新增</el-button
        >
        <!-- 新增 end -->

        <!-- 导出 Excel start -->
        <el-button
          type="primary"
          :loading="downloadLoading"
          icon="el-icon-download"
          @click="exportExcel"
          >导出Excel</el-button
        >

        <!-- 隐藏的a标签，用来导出excel -->
        <a style="display: none;" id="exportA" :href="downloadUrl" download></a>
        <!-- 导出 Excel end -->

        <!-- 导入EXCEL start -->
        <el-upload
          ref="uploadImport"
          action="https://run.mocky.io/v3/9d059bf9-4660-45f2-925d-ce80ad6c4d15"
          :limit="1"
          :on-exceed="handleImportExceed"
          :on-change="handleImportChange"
          :show-file-list="false"
          accept="application/vnd.openxmlformats-officedocument.spreadsheetml.sheet"
          :auto-upload="false"
          mr-3
          flex
          justify-end
        >
          <el-button
            type="primary"
            :loading="importLoading"
            icon="el-icon-upload2"
            >导入EXCEL</el-button
          >
          <!-- <template #tip>
            <div class="el-upload__tip">
              jpg/png files with a size less than 2M.
            </div>
          </template> -->
        </el-upload>
        <!-- 导入EXCEL end -->
      </el-header>
      <el-main>
        <!-- 表格 start -->
        <el-table
          class="table"
          :data="tableData"
          style="width: 100%"
          border
          v-loading="loadingTable"
          :header-cell-style="{
            background: '#f2f2f2',
            color: '#606266',
            height: '38px',
            padding: '0px'
          }"
          :cell-style="{ height: '38px', padding: '0' }"
        >
          <el-table-column
            v-for="(value, key) in tableFields"
            :key="key"
            :prop="value"
            :label="key"
            min-width="130"
            align="center"
          >
            <template slot-scope="scope">
              <div>
                {{ tableContentValue(scope.row, value) }}
              </div>
            </template>
          </el-table-column>
          <el-table-column label="操作" min-width="130">
            <template slot-scope="scope">
              <el-button
                @click.native.prevent="editRow(scope.$index, scope.row)"
                type="text"
                size="small"
              >
                编辑
              </el-button>
              <el-button
                @click.native.prevent="deleteRow(scope.$index, scope.row)"
                type="text"
                size="small"
              >
                删除
              </el-button>
            </template>
          </el-table-column>
        </el-table>
        <!-- 表格 end -->

        <!-- 导出的表格 start -->
        <!-- 此表格始终保持隐藏 -->
        <el-table
          id="outTable"
          class="table"
          :data="exportTableData"
          style="width: 100%; display: none"
          border
        >
          <el-table-column
            v-for="(value, key) in tableFields"
            :key="key"
            :prop="value"
            :label="key"
            min-width="130"
            align="center"
          >
          </el-table-column>
        </el-table>
        <!-- 导出的表格 end -->
      </el-main>
      <el-footer>
        <!-- 分页 start -->
        <div class="block">
          <el-pagination
            background
            layout="prev, pager, next, total, sizes, jumper"
            :total="total"
            :current-page="currentPage"
            :page-sizes="[10, 20, 30, 40, 50, 60, 70, 80, 90]"
            :page-size="pageSize"
            @current-change="onCurrentChange"
            @size-change="onSizeChange"
          >
          </el-pagination>
        </div>
        <!-- 分页 end -->
      </el-footer>

      <!-- dialog对话框 start -->
      <el-dialog
        class="dialog"
        :title="dialogTitle"
        :visible.sync="dialogVisible"
        :before-close="handleClose"
        :close-on-click-modal="false"
      >
        <!-- form表单 -->
        <el-form
          :model="validateForm"
          ref="validateForm"
          label-width="100px"
          class="demo-ruleForm"
          :rules="validateFormRules"
        >
          <el-form-item label="仓库名称" prop="spotId">
            <!-- 选择仓库名称 start -->
            <el-select
              v-model="validateForm.spotId"
              filterable
              placeholder="请选择仓库名称"
              clearable
              style="width: 100%"
              @change="onChangeSpot"
            >
              <el-option
                v-for="(item, index) in spotNameOptions"
                :key="index"
                :label="item.name"
                :value="item.id"
              >
              </el-option>
            </el-select>
            <!-- 选择仓库名称 end -->
          </el-form-item>



          <!-- 配件名称 start -->
          <el-form-item label="配件名称" prop="name">
			  <!-- 选择配件编码 start -->
			  <el-select
			    v-model="validateForm.managementId"
			    filterable
			    placeholder="请选择配件编码"
			    clearable
			    style="width: 100%"
			    @change="onChangeCode"
			  >
			    <el-option
			      v-for="(item, index) in codeOptions"
			      :key="index"
			      :label="item.name"
			      :value="item.id"
			    >
			    </el-option>
			  </el-select>
			  <!-- 选择配件编码 end -->
<!--           <el-card class="box-card">
              <span class="card-text" v-if="!validateForm.managementId"
                >根据配件编码获取</span
              >
              <span v-else>{{ validateForm.name }}</span>
            </el-card> -->
          </el-form-item>
          <!-- 配件名称 end -->

			<el-form-item label="配件编码" prop="managementId">
				<el-card class="box-card">
				   <span class="card-text" v-if="!validateForm.managementId"
				     >根据配件编码获取</span
				   >
				   <span v-else>{{ validateForm.code }}</span>
				 </el-card>
			  <!-- 选择配件编码 start -->
			  <!-- <el-select
			    v-model="validateForm.managementId"
			    filterable
			    placeholder="请选择配件编码"
			    clearable
			    style="width: 100%"
			    @change="onChangeCode"
			  >
			    <el-option
			      v-for="(item, index) in codeOptions"
			      :key="index"
			      :label="item.code"
			      :value="item.id"
			    >
			    </el-option>
			  </el-select> -->
			  <!-- 选择配件编码 end -->
			</el-form-item>

          <!-- 配件类型 start -->
          <!-- <el-form-item label="配件类型" prop="model">
            <el-card class="box-card">
              <span class="card-text" v-if="!validateForm.managementId">根据配件编码获取</span>
              <span v-else>{{ validateForm.model }}</span>
            </el-card>
          </el-form-item> -->
          <!-- 配件类型 end -->

          <!-- 单位 start -->
          <el-form-item label="单位" prop="unit">
            <el-card class="box-card">
              <span class="card-text" v-if="!validateForm.managementId"
                >&nbsp;根据配件编码获取</span
              >
              <span v-else>&nbsp;{{ validateForm.unit }}</span>
            </el-card>
          </el-form-item>
          <!-- 单位 end -->

          <!-- 数量 start -->
          <el-form-item label="数量" prop="amount" v-if="getAmout">	
            <el-input
              type="text"
              v-model="validateForm.amount"
              autocomplete="off"
              placeholder="请输入数量"
            ></el-input>
          </el-form-item>
		  <el-form-item label="数量" prop="amount" v-if="!getAmout">
		  		<el-card class="box-card" >
		  			<span>{{ validateForm.amount }}</span>
		  		</el-card>
		  </el-form-item>
          <!-- 数量 end -->

          <!-- 阈值 start -->
          <el-form-item label="阈值" prop="threshold">
            <el-input
              type="text"
              v-model="validateForm.threshold"
              autocomplete="off"
              placeholder="请输入阈值"
            ></el-input>
          </el-form-item>
          <!-- 阈值 end -->

          <!-- 备注 start -->
          <el-form-item label="备注" prop="notes">
            <el-input
              type="text"
              v-model="validateForm.notes"
              autocomplete="off"
              placeholder="请输入备注"
            ></el-input>
          </el-form-item>
          <!-- 备注 end -->
        </el-form>
        <!-- form表单 -->
        <!-- 提交 start -->
        <div class="submit">
          <el-button type="primary" @click="submitForm('validateForm')"
            >提交</el-button
          >
          <el-button @click="dialogClose">取消</el-button>
        </div>
        <!-- 提交 end -->
      </el-dialog>
      <!-- dialog对话框 end -->
    </el-container>
  </div>
</template>

<script>
// import JsonExcel from 'vue-json-excel'
import FileSaver from 'file-saver'
import XLSX from 'xlsx'

import { createUrl } from '../utils/utils.js'
export default {
  name: 'Address',
  data() {
    /**
     * 公共验证函数(验证数字值)
     * @params message - 提示信息
     */
    function checkNumberMethod(name = '') {
      // 返回验证函数
      return (rule, value, callback) => {
        if (!value) {
          // 如果值为空，可以回调
          callback()
        }

        if (isNaN(Number(value))) {
          callback(new Error(name + '必须为数字值'))
        } else {
          callback()
        }
      }
    }

    /**
     * 公共验证函数(验证必填数字值)
     * @params message - 提示信息
     */
    function checkRequireNumberMethod(name = '') {
      // 返回验证函数
      return (rule, value, callback) => {
        if (!value) {
          return callback(new Error(name + '不能为空'))
        }

        if (isNaN(Number(value))) {
          callback(new Error(name + '必须为数字值'))
        } else {
          callback()
        }
      }
    }

    /**
     * 公共验证函数(验证必填整数值)
     */
    function checkIntegerMethod(name = '') {
      // 返回验证函数
      return (rule, value, callback) => {
        if (!value) {
          callback()
        }

        function isInteger(obj) {
          return obj % 1 === 0
        }

        if (!isInteger(value)) {
          callback(new Error(name + '必须为整数值'))
        } else {
          callback()
        }
      }
    }

    /**
     * 公共验证函数(验证必填整数值)
     */
    function checkRequireIntegerMethod(name = '') {
      // 返回验证函数
      return (rule, value, callback) => {
        if (!value) {
          return callback(new Error(name + '不能为空'))
        }

        function isInteger(obj) {
          return obj % 1 === 0
        }

        if (!isInteger(value)) {
          callback(new Error(name + '必须为整数值'))
        } else {
          callback()
        }
      }
    }

    /**
     * 公共验证函数(验证必填值)
     * @params message - 提示信息
     */
    function checkRequireMethod(name = '') {
      // 返回验证函数
      return (rule, value, callback) => {
        if (!value) {
          return callback(new Error(name + '不能为空'))
        } else {
          callback()
        }
      }
    }

    return {
      downloadLoading: false, // 导出excel按钮的加载动画
      importLoading: false, // 导入excel按钮的加载动画
      
      // input: '', // 请输入公司名称
      tableFields: {
        //导出Excel表格的表头设置
        仓库名称: 'spotName',
        配件编码: 'code',
        配件名称: 'name',
        // "配件类型": "model",
        单位: 'unit',
        数量: 'amount',
        阈值: 'threshold',
        备注: 'notes',
        更新时间: 'updateTime'
      },
      tableData: [], // 表格数据
      exportTableData: [], // 导出的全部表格数据
      downloadLoading: false, // 导出excel按钮的加载动画

      loadingTable: false,

      currentPage: 1, // 当前页数,
      pageSize: 10, // 每页显示条数

      dialogVisible: false, // 是否显示dialog对话框
      dialogState: '', // dialog是新增还是编辑表单，新增为'add'，编辑为'edit'

      searchSpotNameOptions: [], // 搜索时仓库名称选项
      spotNameOptions: [], // 提交时仓库名称选项

      searchNameOptions: [], // 搜索时配件名称选项
      codeOptions: [], // 提交时配件名称选项

      searchThresholdOptions: [
        {
          value: '0',
          label: '大于等于阈值'
        },
        {
          value: '1',
          label: '低于阈值'
        }
      ], // 搜索时阈值选项

      searchValidateForm: {
        managementId: '',
        spotId: '',
        isDanger: ''
      }, // 搜索表单验证对象
      searchVF: {
        managementId: '',
        spotId: '',
        isDanger: ''
      }, // 搜索需要提交的表单
      validateForm: {},
      total: 0,

      downloadUrl: '',

      dialogTitle: '',

      // 搜索表单验证规则
      searchValidateFormRules: {},

      // 提交表单验证规则
      validateFormRules: {
        // 仓库名称
        spotName: [
          {
            validator: checkRequireMethod('仓库类型'),
            required: true,
            trigger: ['blur', 'change']
          }
        ],

        // 配件编码
        managementId: [
          {
            validator: checkRequireMethod('配件编码'),
            required: true,
            trigger: ['blur', 'change']
          }
        ],

        // 数量
        amount: [
          {
            validator: checkRequireIntegerMethod('数量'),
            required: true,
            trigger: ['blur', 'change']
          }
        ],

        // 阈值
        threshold: [
          { validator: checkIntegerMethod('阈值'), trigger: ['blur', 'change'] }
        ]
      },
	  getAmout:true,
    }
  },
  created() {
    // 如果当前环境是开发环境才调用登录方法
    if (process.env.NODE_ENV == 'development') {
      this.login()
    }

    this.initTableData()
    this.getAllGoods()
    this.getAllSpot()
    this.getValidateForm()
  },
  methods: {
    // 获取表单验证对象
    getValidateForm() {
      var validateForm = {}
      for (var item in this.tableFields) {
        validateForm[this.tableFields[item]] = ''
      }
      this.validateForm = validateForm
      // console.log(this.validateForm);
    },

    // 登录(必须)
    async login() {
      const { data: res } = await this.$http.post(
        '/loginSystem?username=lijiazhao&password=lijiazhao'
      )
    },

    /**
     * 初始化表格数据
     */
    async initTableData() {
      var managementId = this.searchVF.managementId
      if (managementId == '' || !managementId) {
        managementId = null
      }

      var spotId = this.searchVF.spotId
      if (spotId == '' || !spotId) {
        spotId = null
      }

      var isDanger = this.searchVF.isDanger
      if (isDanger == '' || !isDanger) {
        isDanger = null
      }

      this.loadingTable = true
      const { data: res } = await this.$http.get('/system/goods_stock/list', {
        params: {
          managementId: managementId, // 配件id
          spotId: spotId, // 仓库姓名
          isDanger: isDanger, // 阈值状态
          pageNum: 0, // 页码
          pageSize: this.pageSize // 每页条数
        }
      })
      // console.log(res);
      if (res.state == '200') {
        this.tableData = res.data.list
        this.total = res.data.total
        this.loadingTable = false
      } else {
        this.$message({
          message: res.msg,
          type: 'error',
          offset: 100,
          center: true,
          duration: 3000
        })
        return
      }
    },

    /**
     * 分页查询表格数据
     */
    async pageTableData() {
      var managementId = this.searchVF.managementId
      if (managementId == '' || !managementId) {
        managementId = null
      }

      var spotId = this.searchVF.spotId
      if (spotId == '' || !spotId) {
        spotId = null
      }

      var isDanger = this.searchVF.isDanger
      if (isDanger == '' || !isDanger) {
        isDanger = null
      }

      this.loadingTable = true
      const { data: res } = await this.$http.get('/system/goods_stock/list', {
        params: {
          managementId: managementId, // 配件id
          spotId: spotId, // 仓库姓名
          isDanger: isDanger, // 阈值状态
          pageNum: (this.currentPage - 1) * this.pageSize, // 页码
          pageSize: this.pageSize // 每页条数
        }
      })
      // console.log(res);
      if (res.state == '200') {
        this.tableData = res.data.list
        this.total = res.data.total
        this.loadingTable = false
      } else {
        this.$message({
          message: res.msg,
          type: 'error',
          offset: 100,
          center: true,
          duration: 3000
        })
        return
      }
    },

    /**
     * 获取所有配件信息列表
     */
    async getAllGoods() {
      const { data: res } = await this.$http.get('/system/goods_stock/getGoods')
	  console.log(res)
      this.searchNameOptions = res.data
      this.codeOptions = res.data
    },

    /**
     * 获取所有仓库信息列表
     */
    async getAllSpot() {
      const { data: res } = await this.$http.get('/system/goods_stock/getSpot')

      this.searchSpotNameOptions = res.data
      this.spotNameOptions = res.data
    },

    /**
     * 根据仓库和配件id获取仓库是否有这个配件
     */
    async isGoodsIdSpotId(spotId, managementId) {
      const { data: res } = await this.$http.get('/system/goods_stock/isGoodsIdSpotId', {
        params: {
          spotId,
          managementId
        }
      })

      console.log(res);
      if(res.state == '200') {
        return res.data
      }
    },

    /**
     * 新增表格数据
     */
    async addTableData() {
      const { data: res } = await this.$http.post(
        '/system/goods_stock/add',
        this.validateForm
      )
      if (res.state == '200') {
        this.$message({
          message: '添加成功',
          type: 'success',
          offset: 100,
          center: true,
          duration: 1000
        })
        this.initTableData()
        this.$refs.validateForm.resetFields() // 重置表单
      } else {
        this.$message({
          message: res.msg,
          type: 'error',
          offset: 100,
          center: true,
          duration: 3000
        })
      }
    },

    /**
     * 修改表格数据
     */
    async editTableData() {
      const { data: res } = await this.$http.post(
        '/system/goods_stock/edit',
        this.validateForm
      )
      if (res.state == '200') {
        this.$message({
          message: '编辑成功',
          type: 'success',
          offset: 100,
          center: true,
          duration: 1000
        })
        this.initTableData()
      } else {
        this.$message({
          message: res.msg,
          type: 'error',
          offset: 100,
          center: true,
          duration: 3000
        })
      }
    },

    /**
     * 查询公司名称获取列表数据
     */
    getTableData() {
      // console.log(this.searchValidateForm.type, this.searchInput);
      // 如果输入框中的内容等于上次搜索的内容，则直接返回
      if (
        this.searchValidateForm.managementId == this.searchVF.managementId &&
        this.searchValidateForm.spotId == this.searchVF.spotId &&
        this.searchValidateForm.isDanger == this.searchVF.isDanger
      ) {
        return
      }

      // 保存当前搜索的内容
      this.searchVF.managementId = this.searchValidateForm.managementId
      this.searchVF.spotId = this.searchValidateForm.spotId
      this.searchVF.isDanger = this.searchValidateForm.isDanger

      // 否则调用接口，请求数据，为tableData重新赋值
      console.log('调用接口')
      this.initTableData()
    },

    /**
     * 导出Excel表格的全部数据
     */
    async getExportTableData() {
      var managementId = this.searchVF.managementId
      if (managementId == '' || !managementId) {
        managementId = null
      }

      var spotId = this.searchVF.spotId
      if (spotId == '' || !spotId) {
        spotId = null
      }

      var isDanger = this.searchVF.isDanger
      if (isDanger == '' || !isDanger) {
        isDanger = null
      }

      var params = {
        url: '/system/goods_stock/downloadExcel',
        managementId, // 配件id
        spotId, // 仓库姓名
        isDanger, // 阈值状态
      }

      var downloadUrl = createUrl(params)

      console.log(downloadUrl);

      this.downloadUrl = downloadUrl
      this.$nextTick(() => {
        var exportAEle = document.getElementById('exportA')
        exportAEle.click()
      })
    },

    // 导入EXCEL
    async importE(file) {
      this.importLoading = true
      var formData = new FormData()
      formData.append('file', file)

      const { data: res } = await this.$http.post(
        '/system/goods_stock/importExcel',
        formData
      )
      this.importLoading = false
      if (res.state == '200') {
        this.$message({
          message: '导入成功！',
          type: 'success'
        })
        this.initTableData()
      } else {
        this.$message({
          message: res.msg,
          type: 'error'
        })
      }
    },

    // 点击搜索按钮时触发
    onSearch(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.getTableData()
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },

    // 新增
    onAdd() {
	  this.getAmout = true
      this.dialogVisible = true
      this.dialogState = 'add'
      this.dialogTitle = '新增库存管理'
    },

    // 点击编辑时触发
    editRow(index, row) {
	  this.getAmout = false
      // console.log(index, row);
      this.dialogVisible = true // 是否显示编辑dialog对话框
      var rowObj = Object.assign({}, row) // 深拷贝row对象
      // rowObj.userName = parseFloat(rowObj.userName)
      this.validateForm = rowObj
      this.dialogState = 'edit'
      this.dialogTitle = '编辑库存管理'
    },

    // 点击删除时触发
    deleteRow(index, row) {
      // console.log(index, row);
      this.$confirm('此操作将删除该行数据, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
        .then(async () => {
          // let data = this.$qs.stringify({
          //   id: row.id
          // })
          const { data: res } = await this.$http.delete(
            '/system/goods_stock/delete',
            {
              params: {
                id: row.id
              }
            }
          )
          // console.log(res);
          if (res.state == '200') {
            this.$message({
              type: 'success',
              message: '删除成功',
              offset: 100,
              center: true,
              duration: 1000
            })
            this.initTableData()
          } else {
            this.$message({
              type: 'error',
              message: res.msg,
              offset: 100,
              center: true,
              duration: 3000
            })
          }
        })
        .catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除',
            offset: 100,
            center: true,
            duration: 1000
          })
        })
    },

    // 编辑dialog取消
    dialogClose() {
      this.dialogVisible = false
      this.$refs.validateForm.resetFields() // 重置表单（清空无效）
      this.getValidateForm() // 将validateForm对象中各个属性清空
    },

    // 编辑dialog 右上角关闭按钮
    handleClose(done) {
      done() // 关闭dialog对话框
      this.$refs.validateForm.resetFields() // 重置表单（清空无效）
      this.getValidateForm() // 将validateForm对象中各个属性清空
    },

    // 提交表单中仓库名称选项发生变化时触发
    onChangeSpot(spotId) {
      // console.log(spotId);
      this.spotNameOptions.forEach((item) => {
        if (item.id == spotId) {
          this.validateForm.spotName = item.name
        }
      })
    },

    // 提交表单中配件名称选项发生变化时触发
    onChangeCode(managementId) {
      // console.log(managementId);
      this.codeOptions.forEach((item) => {
        if (item.id == managementId) {
          this.validateForm.code = item.code
          this.validateForm.name = item.name
          this.validateForm.model = item.model
          this.validateForm.unit = item.unit
        }
      })
    },

    // dialog提交
    submitForm(formName) {
      this.$refs[formName].validate(async (valid) => {
        if (valid) {
          console.log(this.validateForm)
          if (this.dialogState == 'add') {
            var { spotId, managementId } = this.validateForm

            var isGoods = await this.isGoodsIdSpotId(spotId, managementId)
            if(isGoods == 1) {
              this.$message({
                message: '该库房已存在该配件，请直接编辑',
                type: 'error',
                offset: 100,
                center: true,
                duration: 3000
              })
              return
            }
            // 新增公司信息
            console.log('新增公司信息')
            this.addTableData()
          } else if (this.dialogState == 'edit') {
            // 编辑公司信息
            console.log('编辑公司信息')
            this.editTableData()
          }
          this.dialogVisible = false
          this.$refs.validateForm.resetFields() // 重置表单
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },

    // currentPage 改变时会触发
    onCurrentChange(currentPage) {
      this.currentPage = currentPage
      this.pageTableData()
    },

    // pageSize 改变时会触发
    onSizeChange(pageSize) {
      // console.log(pageSize);
      this.pageSize = pageSize
      this.pageTableData()
    },

    // 导出Excel表格
    exportExcel() {
      this.$confirm('确认导出Excel表格?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
        .then(async () => {
          this.downloadLoading = true

          await this.getExportTableData()
          this.downloadLoading = false
          
          this.$message({
            type: 'success',
            message: '已成功导出Excel表格！'
          })
        })
        .catch(() => {
          this.$message({
            type: 'info',
            message: '已取消导出Excel表格'
          })
        })
    },

    // 导入EXCEL文件超出限制时触发
    handleImportExceed(files) {
      this.$refs.uploadImport.clearFiles()
      // const file = files[0]
      this.$refs.uploadImport.submit(files)
    },

    // 导入EXCEL文件状态改变时触发
    handleImportChange(uploadFile, uploadFiles) {
      console.log(uploadFile)
      this.importE(uploadFile.raw)
    }
  },
  components: {
    // DownloadExcel: JsonExcel,
  },
  computed: {
    tableContentValue() {
      return (row, value) => {
        // console.log(value);
        var result = ''
        if (value == 'type') {
          if (row[value] == 1) {
            result = '景区地址'
          } else if (row[value] == 2) {
            result = '工厂地址'
          } else if (row[value] == 3) {
            result = '库房地址'
          }
        } else {
          result = row[value]
        }
        return result
      }
    }
  }
}
</script>

